<template>
    <div class="activities" v-loading="isLoading">
        <el-tabs v-model="campus">
            <el-tab-pane v-for="(v, i) in campuses" :key="i" :label="v" :name="i" />
        </el-tabs>
        <el-table style="width: 100%"
            v-if="items.length" :data="items" @row-click="rowClick"
            :row-style="{cursor: 'pointer'}">
            <el-table-column prop="title" label="名称" />
            <el-table-column prop="site" label="地点" />
        </el-table>
        <el-empty v-else description="该校区暂无志愿活动。" />
    </div>
</template>

<script>
import AV from 'leancloud-storage';
export default {
    name: 'Activities',
    data () {
        return {
            isLoading: true,
            campuses: ['西土城', '沙河', '宏福'],
            campus_: 0,
            items: [],
        }
    },
    computed: {
        campus: {
            get () { return this.campus_ },
            set (v) {
                this.isLoading = true
                this.campus_ = v
                const query = new AV.Query('Activity')
                query.equalTo('campus', Number(this.campus))
                query.find().then(res => {
                    this.isLoading = false
                    this.items = res.map(i => i.toJSON())
                }).catch(e => {
                    this.isLoading = false
                    this.$message({
                        message: `[${e.code}] ${e.rawMessage}`,
                        type: 'warning'
                    })
                })
            }
        }
    },
    beforeMount () {
        this.campus = AV.User.current().get('campus')
    },
    methods: {
        rowClick (row) {
            this.$router.push(`/activities/${row.objectId}`)
        },
    },
}
</script>
